import dynamic from "next/dynamic";
import { Layout } from "antd";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { FGetMe, FGetSidebarMenu } from "@function";
import { updateOrganization, updateSidebar, updateUser } from "@action";
import { useRouter } from "next/router";
import CApi from "@api";

const CHeader = dynamic(() => import("./CHeader"), {
  ssr: false,
});
const CSidebar = dynamic(() => import("./CSidebar"), {
  ssr: false,
});
const CFooter = dynamic(() => import("./CFooter"), {
  ssr: false,
});

const { Content } = Layout;
const CLayout = ({ children }) => {
  const dispatch = useDispatch();
  const router = useRouter();
  const [collapsed, setCollapsed] = useState(false);
  const [height, setHeight] = useState(500);
  const toggle = () => {
    setCollapsed(!collapsed);
  };
  useEffect(() => {
    FGetSidebarMenu(updateListMenu);
    FGetMe(router, updateUserData);
    getListOrganization();
    setHeight(document.body.clientHeight - 80.1);
    window.addEventListener("resize", () => {
      setHeight(document.body.clientHeight - 80.1);
    });
  }, []);
  const getListOrganization = async () => {
    await CApi({
      url: "api/list-organization",
      method: "GET",
    }).then((res) => {
      dispatch(updateOrganization(res.data));
    });
  };

  const updateListMenu = (data) => {
    dispatch(updateSidebar(data));
  };
  const updateUserData = (data) => {
    dispatch(updateUser(data));
  };

  return (
    <Layout>
      <CHeader toggle={toggle} collapsed={collapsed} />
      <Layout>
        <CSidebar toggle={toggle} height={height} collapsed={collapsed} />
        <Layout>
          <Content
            style={{
              overflow: "auto",
              height: height - 70,
              marginTop: "80px",
              marginLeft: "15px",
              marginRight: "15px",
            }}
          >
            {children}
            <div style={{ marginBottom: "1.5%" }} />
          </Content>
          <CFooter />
        </Layout>
      </Layout>
    </Layout>
  );
};

export default CLayout;
